import { Injectable } from '@angular/core';
import { HttpClient, HttpHandler, HttpHeaders } from '@angular/common/http';


import { appConfig } from '../server/app.config';

// config
import { clsCiudad } from '../models/clsCiudad';
import { clsTrabajo } from '../models/clsTrabajo';
import { clsModalidad } from '../models/clsModalidad';

@Injectable()
export class ConfigService {  
  private urlModalidades = appConfig.apiUrl + 'api/config/modalidad';
  private urlModalidades2 = appConfig.apiUrl + 'api/config/modalidad2';
  private urlProfesiones = appConfig.apiUrl + 'api/config/profesion';
  private urlTrabajos = appConfig.apiUrl + 'api/config/trabajo';
  private urlTrabajos2 = appConfig.apiUrl + 'api/config/trabajo2';
  private urlRangos = appConfig.apiUrl + 'api/config/rango';
  private urlCiudades = appConfig.apiUrl + 'api/config/ciudad';
  private urlTarifas = appConfig.apiUrl + 'api/config/tarifa';

  constructor(private http: HttpClient) {
    console.log('servicio activo');
  }


 /* -------------------- GET DE CONFIGURACION ------------------ */
  getModalidad() {
    return this.http.get<clsModalidad[]>(this.urlModalidades);
  }

  getCiudad() {
  return this.http.get<clsCiudad[]>(this.urlCiudades);
  }

  getTrabajo() {
   return this.http.get<clsTrabajo[]>(this.urlTrabajos);
  }
 /* -------------------- GET DE CONFIGURACION ------------------ */

  /* -------------------- SET DE CONFIGURACION ------------------ */
  postModalidades(datos) {
 const headers = new HttpHeaders().set('Content-Type', 'application/json');
    return this.http.post(this.urlModalidades, datos, { headers: headers });
  }

  postCiudades(datos) {
    const headers = new HttpHeaders().set('Content-Type', 'application/json');
    return this.http.post(this.urlCiudades, datos, { headers: headers });
  }

  postTrabajos(datos) {
    const headers = new HttpHeaders().set('Content-Type', 'application/json');
    return this.http.post(this.urlTrabajos, datos, { headers: headers });
  }
  
/* -------------------- SET DE CONFIGURACION ------------------ */
  deleteTrabajos(id) {
    return this.http.delete(this.urlTrabajos + '/' + id);
  }
  deleteCiudades(id) {
    return this.http.delete(this.urlCiudades + '/' + id);
  }
  deleteModalidades(id) {
    return this.http.delete(this.urlModalidades + '/' + id);
  }



}




