import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { MyApp } from './app.component';

import { HomePage } from '../pages/home/home';

//submodulo
import { AvisoPage } from '../pages/busqueda/aviso/aviso';
import { BusquedaTrabajadoresComponent } from '../pages/busqueda/candidatos/busqueda/busqueda-trabajadores.component';

import { NgForm } from '@angular/forms';
//import { NgClass } from '@angular/common';
//import { RouterModule, Route } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { ConfigService } from '../controllers/config.service';
import { UserPageModule } from "../pages/user/user.module";

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    AvisoPage,
   BusquedaTrabajadoresComponent,
 
    //add    
    /*
    FormsModule,
    HttpClientModule
    */
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
  //  NgClass
    FormsModule,
    HttpClientModule,
    UserPageModule
  ],
  bootstrap: [IonicApp],
  
  entryComponents: [
    MyApp,
    HomePage,
    AvisoPage,
    BusquedaTrabajadoresComponent,
  
    /*
    FormsModule,
    HttpClientModule
    */
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ConfigService
  ]


})
export class AppModule {}



