    imprimirCandidato() {
        $('#contratocandidato').modal('toggle')
        var options = [
            'height=720',
            'width=1280'
        ]
        var mywindow = window.open('', 'PRINT', options.join());
        mywindow.document.write(`<html><head>
				<style>
						@page {
							size: letter;
							margin: 0;
						}
						body {
							margin: 1.6cm;
						}
						#contratotitle {
							text-align: center;
							font-size: 24px
						}
						#contratologo {
							float:left;
						}


						#contratotabla {
						  width: 100%;
						  border: 1px solid #000;
						  margin-bottom: 50px;
						}
						#contratotabla caption {
							border-top: 1px solid #000;
							border-left: 1px solid #000;
							border-right: 1px solid #000;
							text-align: left;
							padding: 5px 5px;
							background-color: grey !important;
							-webkit-print-color-adjust: exact;
						}
						#contratotabla tbody > tr > td:first-child {
							width:250px;
							text-align: right;
							padding-right: 30px;
						}
						#contratotabla tbody > tr:nth-child(9) {
							border: 1px solid #000;
						}


						#toc {
							font-size: 9px;
							text-align: justify;
							margin-bottom: 100px;
						}
						#signature {
							text-align: center
						}
						.uppercase {
							text-transform: uppercase;
						}
					}
				</style>
			`);
        mywindow.document.write('</head><body >');
        mywindow.document.write(document.getElementById('bodyprintcandidato').innerHTML);
        mywindow.document.write('</body></html>');
        mywindow.document.close();
        mywindow.focus();
        mywindow.print();
        mywindow.close();
    }

